import { Input,Button,Avatar,Tag } from "antd"
import { UserOutlined,GithubOutlined } from "@ant-design/icons"
import { useState } from "react"
import {sendMsg,TResponse} from '../utils/api'


const UserItem  = (props:any)=>{
    
    return (
        <div style={{display:'flex',alignItems:'start',justifyContent:'right'}}>
            <Tag color="lime" style={{wordBreak:'break-all',whiteSpace:'break-spaces'}} >{props.msg}</Tag>
            <Avatar style={{background:'#87d068',flexShrink:0}} size={'small'} icon={<UserOutlined />}></Avatar>
        </div>
    )
}

const SystemItem = (props:any)=>{
    return (
        <div style={{display:'flex',alignItems:'start',justifyContent:'left'}}>
            <Avatar style={{flexShrink:0}} size={"small"} icon={<GithubOutlined />} ></Avatar>
            <Tag color="orange" style={{wordBreak:'break-all',whiteSpace:'break-spaces'}}>{props.msg}</Tag>
        </div>
    )
}



const Chat = ()=>{
    const [text,setText] = useState<string>('')
    const [messageList,setMessageList] = useState<{role:string,message:string}[]>([])
    const handleClick =async ()=>{
        const message:{role:string,message:string} = {role:'user',message:text}
        setText('')
        setMessageList((msgList)=>[...msgList,message])

        try{
            const result = await sendMsg(text)
        const data:TResponse = result.data;
        let msg = ''
        data.choices.map(item=>{
            if(item.message.role!=='user')
                msg+=item.message.content
        })
        setMessageList((msgList)=>[...msgList,{role:'system',message:msg}])
        }catch(err){
            console.log(err);
        }
    }

    return (
        <div style={{display:"flex",flexDirection:'column',width:'100%',maxWidth:750,margin:'0 auto',height:'98vh',rowGap:10}}>
            <div className="show" style={{flex:1,overflow:'auto',flexDirection:'column',gap:5,display:'flex'}}>
                {messageList.map((item,index)=>{
                    if(item.role==='system'){
                        return (
                            <SystemItem msg={item.message} key={index} />
                        )
                    }else{
                        return (
                            <UserItem msg={item.message} key={index} />
                        )
                    }
                })}
            </div>
            <div className="input" style={{display:'flex',columnGap:15}}>
                <Input placeholder="Input something to start conversation..." value={text} onChange={e=>{setText(e.target.value)}} onKeyDown={e=>{
                    if(e.key==='Enter'){
                        handleClick()
                    }
                }} />
                <Button onClick={handleClick} ></Button>
            </div>
        </div>
    )
}



const Home = () =>{
    return (
        <div>
            <Chat />
        </div>
    )
}

export default Home