import axios from "axios";

const instance = axios.create({
    headers:{
        'Content-Type':'application/json',
        'Authorization':'Bearer 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'
    },
    baseURL:'https://openai.wndbac.cn'
})

export const sendMsg = (text:string)=>{
    const reqBody ={
        model:'gpt-3.5-turbo',
        messages:[
            {
                role:'user',
                content:text
            }
        ]
    }
    return instance.post(`/v1/chat/completions`,reqBody)
}

export type TResponse={
    id:string,
    object:'chat.completion',
    created:number,
    model:string,
    choices:{index:number,message:{role:string,content:string},finish_reason:string}[],
    usage:{
        prompt_tokens:number,
        completion_tokens:number,
        total_tokens:number
    }
}
